import {fetch} from '@/utils/fetch';

/**
 * 
 * @param {*}  params
 */
export const applyQuery = (params) => fetch({
  url: 'api/hrApply/query',
  method: 'post',
  data: params
});

/**
 * 
 * @param {*}  params
 */
export const applyCreate = (params) => fetch({
  url: 'api/hrApply/create',
  method: 'post',
  data: params
});

/**
 * 
 * @param {*}  http://localhost:8070/hmap/api/hrApply/deleteAll
 */
export const deleteAll = (params) => fetch({
  url: 'api/hrApply/deleteAll',
  method: 'post',
  data: params
});

/**
 * recall
 * @param {*}  http://localhost:8080/hmap/api/hrApply/delete
 */
export const recall = (params) => fetch({
  url: 'api/hrApply/delete',
  method: 'post',
  data: params
});






